
import { ModalOverlay, ModalHeader, Title, ModalContent, ModalBody, ButtonClose } from './styles'

const Modal = ({ 
    children, 
    isOpen,
     onClose, 
     title = null,
     idModal = "modal"
     }) => {
    
    if(!isOpen){
        return null;
    }


    const handleOutsideClik = (e) => {
        if(e.target.id === idModal) onClose()
    }

    return (
        <ModalOverlay id={idModal} onClick={handleOutsideClik}>
            <ModalContent>
                <ModalHeader>
                    {title && (<Title>{title}</Title>)}
                    <ButtonClose onClick={onClose}>X</ButtonClose>
                </ModalHeader>
                <ModalBody>
                    {children}
                </ModalBody>
            </ModalContent>
        </ModalOverlay>
    );
}


export default Modal
