import { useParams } from "react-router";
import CadastroProvider from "./Cadastro";
import { Registre } from "./Components/Registre";

export const Boby = () => {
    const page = (useParams().page)
    switch (page) {
        case "interecesse":
            return <h2>Interecesse</h2>
        default:
            return (<Registre />)
    }
}

export const Cadastro = () => {

    return (
        <CadastroProvider>
            <Boby />
        </CadastroProvider>
    );
}