
import {
    Route,
    Redirect
} from 'react-router-dom'
import { isAuth } from './auth'

export const PrivateRouter = ({component: Component, ...rest}) => {
    return <Route {...rest} render={ props => (
        isAuth()? (
            <Component {...props} />
        ) : (
            <Redirect to={{pathname: "/login", state:{from: props.location } }} />
        )
    )} />
}