const { createContext, useContext, useState } = require("react");

export const Cadastro = createContext();

export default function CadastroProvider({children}) {
    const [user, setUser] = useState({
        nome: '',
        sobrenome: '',
        email: '',
        senha: '',
        nasc: '',
        sexo: '',
        genero: '',
        cidade: '',
        estado: '',
        description: ''
    })
    return (
        <Cadastro.Provider value={{
            user, setUser
        }}>
            {children}
        </Cadastro.Provider>
    );
}

export const useResgiter = () => useContext(Cadastro)