import { Form, Input, Button, Label } from "../../../stylo/styled";

export const Recuperar = () => (
    <Form>
        <Label htmlFor="email">
            Email
        </Label>
        <Input
            type="text"
            name="email"
            autoComplete="off"
            id="email"
        />


        <Button
            type="submit"
            color="rgb(25, 230, 30)"
            size={18}
            border={false}
            boxShadow={true}
            bg="#fff"
        >
            Enviar
        </Button>

    </Form>
)