import { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import styled from "styled-components";
import Modal from "../../components/Modal";
import { Container, Form, Input, Button, Label } from "../../stylo/styled";

import Logo from './../../assets/img/logo.png'

import { Politica } from "./Components/Politica";
import { Recuperar } from "./Components/Recuperar";
import { Termos } from "./Components/Termos";

export const Login = () => {
    const [isOpen, setIsOpen] = useState(null)
    const [name, setName] = useState(null)
    const history = useHistory()
    return (
        <Container>
            <Content>
                <Form>
                    <ImgLogo src={Logo} alt="" />
                    <Label htmlFor="email">
                        Email
                </Label>
                    <Input
                        type="text"
                        name="email"
                        autoComplete="off"
                        id="email"
                    />
                    <Label htmlFor="senha">
                        Senha
                </Label>
                    <Input
                        type="password"
                        name="senha"
                        id="senha"
                    />
                    <Link to="#" onClick={() => {
                        setIsOpen(!isOpen)
                        setName("mail")
                    }}>
                        Esqueceu sua senha?
               </Link>
                    <Button
                        type="submit"
                        color="rgb(25, 230, 30)"
                        size={18}
                        border={false}
                        boxShadow={true}
                        bg="#fff"
                    >
                        Entrar
               </Button>
                    <Button
                        type="button"
                        color="rgb(105, 196, 218)"
                        size={18}
                        border={true}
                        onClick={() => history.push("/cadastro")}
                    >
                        Cadastre-se
               </Button>
                </Form>
            </Content>
            <Footer>
                <p >
                    <span onClick={() => {
                        setIsOpen(!isOpen)
                        setName("termos")
                    }}>Termos de uso</span> - 
                    <span onClick={() => {
                        setIsOpen(!isOpen)
                        setName("poalitica")
                    }}>Políticas de Privacidade</span>
                </p>
            </Footer>

            {name === "mail" && (
                <Modal
                    idModal={name}
                    title="Recuperando Senha"
                    isOpen={isOpen}
                    onClose={() => setIsOpen(!isOpen)}
                >
                    <Recuperar />
                </Modal>
            )}
            {name === "termos" && (
                <Modal
                    title="termos de uso"
                    idModal={name}
                    isOpen={isOpen}
                    onClose={() => setIsOpen(!isOpen)}
                >
                    <Termos />
                </Modal>
            )}

            {name === "poalitica" && (
                <Modal
                    title="termos de uso"
                    idModal={name}
                    isOpen={isOpen}
                    onClose={() => setIsOpen(!isOpen)}
                >
                    <Politica />
                </Modal>
            )}
        </Container>
    );
}

const ImgLogo = styled.img`
    width: 200px;
    margin-top: 15px;
    margin-bottom: 40px;
`

const Footer = styled.footer`
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    p{
        color: #878787;
        font-size: 15px;
    }
`

const Content = styled.div`
    width: 100%;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    a{
        align-self: flex-end;
        margin-top: -3px;
        margin-bottom: 7px;
        font-size: 1rem;
    }
`
